.feature {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;

    text-align: center;
    padding: 3rem 0rem;
    height: auto;
    background-color: rgb(0,0,0,0.05);
    color: var(--color-secondary);
    line-height: 1.5;
}
.feature_title, .feature_subtitle {
    width: 70vw;
    max-width: 780px;
}
.feature_title {
    text-transform: uppercase;
    font-size: 1.75rem;
}
.feature_subtitle {
    font-weight: 700;
}

@media screen and (max-width: 550px) {
    .feature_title {
        font-size: 1.5rem;
    }
    .feature_subtitle {
        font-weight: 500;
    }
}